@font-face {
  font-family: 'as2';
  src:
    url('asset/font/as2.ttf?49v5e6') format('truetype'),
    url('asset/font/as2.woff?49v5e6') format('woff'),
    url('asset/font/as2.svg?49v5e6#as2') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="as2--"], [class*=" as2--"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'as2' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.as2--menu:before {
  content: "\e900";
}
.as2--x:before {
  content: "\e901";
}
